/* ========== General Styling ========== */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
  transition: background-color 0.3s, color 0.3s;
}

.container {
  max-width: 800px;
  margin: auto;
  padding: 20px;
  text-align: center;
}

/* ========== Dark Mode ========== */
body.dark-mode {
  background-color: #121212;
  color: #eee;
}

body.dark-mode .employee-card {
  background-color: #1e1e1e;
  border: 1px solid #444;
}

body.dark-mode .input-field,
body.dark-mode .add-button,
body.dark-mode .edit-button,
body.dark-mode .delete-button {
  background-color: #333;
  color: #eee;
  border: 1px solid #555;
}

/* ========== Heading with Emojis ========== */
h1 {
  font-size: 2rem;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.emoji-burst {
  font-size: 2rem;
  animation: float 1.5s ease-in-out infinite;
}

@keyframes float {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-8px); }
  100% { transform: translateY(0); }
}

/* ========== Form Styling ========== */
.form-group {
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 30px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.input-field {
  padding: 10px;
  margin: 10px 5px;
  width: 90%;
  max-width: 300px;
  border: 1px solid #ccc;
  border-radius: 8px;
}

/* ========== Buttons ========== */
.add-button,
.edit-button,
.delete-button,
.toggle-dark {
  padding: 10px 16px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  margin: 5px;
  font-weight: bold;
  transition: background 0.3s ease;
}

.add-button {
  background-color: #28a745;
  color: white;
}

.edit-button {
  background-color: #007bff;
  color: white;
}

.delete-button {
  background-color: #dc3545;
  color: white;
}

.toggle-dark {
  background-color: #6c757d;
  color: white;
}

/* ========== Employee Card ========== */
.employee-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}

.employee-card {
  background-color: white;
  padding: 20px;
  border-radius: 16px;
  width: 250px;
  text-align: left;
  box-shadow: 0 2px 6px rgba(0,0,0,0.2);
  transition: transform 0.2s ease;
}

.employee-card:hover {
  transform: scale(1.03);
}

/* ðŸŽ‚ Countdown Text */
.countdown {
  font-size: 0.95rem;
  font-style: italic;
  color: #6c757d;
  margin-top: 5px;
}

/* ========== Responsive Design ========== */
@media (max-width: 600px) {
  .employee-card {
    width: 100%;
  }

  .input-field {
    width: 100%;
  }

  h1 {
    font-size: 1.5rem;
    flex-direction: column;
  }
}
.toggle-dark {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 6px 10px;
  font-size: 14px;
  border-radius: 50px;
  background-color: #333;
  color: white;
  border: none;
  cursor: pointer;
}
.footer {
  position: fixed;
  bottom: 10px;
  right: 10px;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 8px 16px;
  border-radius: 12px;
  font-size: 14px;
  font-family: 'Segoe UI', sans-serif;
  z-index: 1000;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
}

.footer:hover {
  background: rgba(0, 0, 0, 0.85);
}
.dark-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 8px 12px;
  background-color: #444;
  color: white;
  border: none;
  border-radius: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  z-index: 10;
}

.dark-toggle:hover {
  background-color: #666;
}
.dark-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 8px 12px;
  background-color: #444;
  color: white;
  border: none;
  border-radius: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  z-index: 1000;
}

/* Dark Mode Styles */
body.dark {
  background-color: #121212;
  color: #ffffff;
}

body.dark .container {
  background-color: #1e1e1e;
}

body.dark .input-field,
body.dark .employee-card {
  background-color: #2a2a2a;
  color: #ffffff;
  border-color: #555;
}

body.dark .add-button,
body.dark .edit-button,
body.dark .delete-button {
  background-color: #444;
  color: white;
}
